/**
 * Template name: <Dashboard Sidebar>
 */
/* Dashboard Sidebar Floating Panel : Custom Styles */
[data-floating-panel-wrapper] {
  display: none !important; }

/* Dashboard Main Nav Wrapper : Custom Styles */
[data-sidebar-main-wrapper][data-is-active="false"] {
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
  transform: scale(0);
  transform-origin: 100% 0%; }

[data-sidebar-main-wrapper][data-is-active="true"] {
  opacity: 1;
  visibility: visible;
  pointer-events: all;
  transform: scale(1);
  transform-origin: 100% 0%; }

/* Dashboard Main Menu Item : Custom Styles */
[data-main-menu-item] {
  min-width: 4.5rem;
  min-height: 4.5rem; }

/**
 * Template name: <Dashboard Submenu>
 */
/* Dashboard Submenu : Custom Styles */
[data-submenu-container][hidden] {
  transform: translateX(100%); }

[data-submenu-container] {
  transform: translateX(0%); }

[data-submenu] [data-submenu-item] {
  padding: 0.375rem 0.5rem;
  /* 6px 8px*/ }

[data-submenu] [data-submenu-item] > span.gap--12 {
  gap: 0.375rem;
  /* 6px*/ }

[data-submenu] [data-submenu-item] > span > .fs--16 {
  font-size: 0.875rem;
  /* 14px */ }

[data-submenu] [data-submenu-item] > span > .lh--24 {
  line-height: 1.25rem;
  /* 20px */ }

[data-submenu] [data-submenu-item][data-has-submenu="true"] > [data-arrow] {
  opacity: 1;
  transform: translateX(0); }
